import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import axiosRequests from '../../../Requests/Requests';
import MobileMenu from '../MobileMenu/MobileMenu';
import DesktopsMenu from '../DesktopsMenu/DesktopsMenu';

import './Burger.css';

export default function Navigation(onHendleGenre) {
    const [configuration, setConfiguration] = useState({});
    const [genre, setGenre] = useState([]);

    useEffect(() => {
        axiosRequests
            .axiosGetConfiguration()
            .then(request => setConfiguration(request.data.images));

        axiosRequests.axiosGenreRequest().then(request => {
            setGenre(request.data.genres);
        });
    }, []);

    if (onHendleGenre.onWidth < 768) {
        return (
            <MobileMenu
                configuration={configuration}
                genre={genre}
                onHendleGenre={onHendleGenre}
            />
        );
    } else {
        return (
            <DesktopsMenu
                configuration={configuration}
                genre={genre}
                onHendleGenre={onHendleGenre}
            />
        );
    }
}

Navigation.propTypes = {
    genre: PropTypes.array,
    configuration: PropTypes.object,
    axiosRequests: PropTypes.object,
    styles: PropTypes.object,
};
