import React, { Fragment, useState, useEffect } from 'react';
import Logo from '../components/Header/Logo/Logo';
import axiosRequests from '../Requests/Requests';
import Spinner from '../components/Main/Spinner/Spinner';
import styles from '../components/Header/Navigation/Navigation.module.css';
import stylesMain from '../components/Main/Main.module.css';
import Movie from '../components/Main/Movie/Movie';
import NotFound from '../views/NotFound';

export default function MovieDetails(props) {
    const [film, setFilm] = useState('');
    const [error, setError] = useState(false);
    const [loading, setLoading] = useState(true);
    const [configuration, setConfiguration] = useState({});

    useEffect(() => {
        setLoading(true);

        axiosRequests
            .axiosMovieDetails(props.match.params.id)
            .then(request => {
                setFilm(request.data);
            })
            .catch(error => {
                return setError(error);
            })
            .finally(() => {
                setLoading(false);
            });

        axiosRequests
            .axiosGetConfiguration()
            .then(request => setConfiguration(request.data.images))
            .catch(error => {
                return setError(error);
            })
            .finally(() => {
                setLoading(false);
            });
    }, [props.match.params.id]);

    return (
        <Fragment>
            <header>
                <div className={styles.wrapHeader}>
                    <Logo />
                </div>
            </header>
            <main>
                {error && <NotFound />}
                {!loading && (
                    <div className={stylesMain.wrapMain}>
                        {film && (
                            <Movie data={film} configuration={configuration} />
                        )}
                        /{' '}
                    </div>
                )}
                {loading && <Spinner />}
            </main>
        </Fragment>
    );
}
