import React, { Fragment, useState, useEffect } from 'react';
import Logo from '../components/Header/Logo/Logo';
import TrendingMovies from '../components/Main/TrendingMovies/TrendingMovies';

import styles from '../components/Header/Navigation/Navigation.module.css';
import stylesQueue from '../components/Main/TrendingMovies/TrendingMovies.module.css';

export default function Queue(props) {
    const [arrMov, setArrMov] = useState([]);
    const [configuration, setConfiguration] = useState([]);

    useEffect(() => {
        let tasks = localStorage.getItem('queue');
        if (tasks) {
            setArrMov(JSON.parse(tasks));
        }
        let config = props.location.state.configuration;
        setConfiguration(config);
    }, [props.location.state.configuration]);

    return (
        <Fragment>
            <header>
                <div className={styles.wrapHeader}>
                    <Logo />
                </div>
            </header>
            <main>
                <div className={stylesQueue.wrapQueueWathcedMain}>
                    {arrMov.length !== 0 && (
                        <TrendingMovies
                            location={props.location.pathname}
                            arrMovies={arrMov}
                            configuration={configuration}
                        />
                    )}
                </div>
            </main>
        </Fragment>
    );
}
