import React, { Fragment, useState, useEffect } from 'react';

import Navigation from '../components/Header/Navigation/Navigation';
import Logo from '../components/Header/Logo/Logo';
import Search from '../components/Header/Search/Search';
import TrendingMovies from '../components/Main/TrendingMovies/TrendingMovies';
import Footer from '../components/Footer/Footer';
import NotFound from '../views/NotFound';

import Spinner from '../components/Main/Spinner/Spinner';

import stylesMain from '../components/Main/Main.module.css';
import styles from '../components/Header/Navigation/Navigation.module.css';

import Routes from '../components/Routes';
import axiosRequests from '../Requests/Requests';

export default function Home(props) {
    const [galleryMovies, setGalleryMovies] = useState([]);
    const [configuration, setConfiguration] = useState({});
    const [loading] = useState(false);
    const [error] = useState(false);
    const [width, setWidth] = useState(window.innerWidth);

    useEffect(() => {
        window.addEventListener('resize', updSize);

        axiosRequests
            .axiosGetConfiguration()
            .then(request => setConfiguration(request.data.images));

        axiosRequests.axiosLoadHome().then(req => {
            setGalleryMovies(req.data.results);
        });
    }, []);

    let updSize = () => {
        setWidth(window.innerWidth);
    };

    useEffect(() => {
        return function cleanup() {
            window.removeEventListener('resize', updSize);
        };
    }, []);

    let handleGenre = (genreID, page = 1) => {
        props.history.push({
            pathname: Routes.genrString + '/' + page + '/' + genreID,
            search: '',
        });
    };
    let handleChangeQuery = searchQuery => {
        props.history.push({
            pathname: Routes.pageString + '=' + 1,
            search: `&query=${searchQuery}`,
        });
    };

    return (
        <Fragment>
            <header>
                <div className={styles.wrapHeader}>
                    <Logo />
                    <Navigation onWidth={width} onHendleGenre={handleGenre} />
                    <Search onRenderSearchRequest={handleChangeQuery} />
                </div>
            </header>
            <main>
                <div className={stylesMain.wrapMain}>
                    {error && <NotFound />}
                    {loading && <Spinner />}
                    {!loading && galleryMovies.length === 0 && <NotFound />}
                    {!loading && galleryMovies.length > 0 && (
                        <TrendingMovies
                            location={props.location.pathname}
                            arrMovies={galleryMovies}
                            configuration={configuration}
                            onWidth={width}
                        />
                    )}
                </div>
            </main>
            <footer>
                <Footer />
            </footer>
        </Fragment>
    );
}
